/*global $ navigator*/
import LeftMenu from './interface/leftmenu'
import Header from './interface/header'
import Footer from './interface/footer'
import { Component } from 'react'
import Link from 'next/link'
import Head from 'next/head'

import NProgress from 'nprogress'
import Router from 'next/router'

Router.events.on('routeChangeStart', (url) => {
  console.log(`Loading: ${url}`)
  NProgress.start()
})
Router.events.on('routeChangeComplete', () => NProgress.done())
Router.events.on('routeChangeError', () => NProgress.done())

export default ({ children,
    title = 'This is the default title',
    
    ogDescription = "Website created with next.js optmized as a PWA",
    ogSiteName = "avn.systems", ogTitle = "Website | avn.systems",
    ogImage = '/static/images/avn.systems.og-image.jpg',
    ogUrl = "https://avn.systems", ogType = "article",
    
    feedJson = "https://avn.systemsm/feed.json",
    feedXml = "https://avn.systems/feed.xml",
    
    canonical = "https://avn.systems",
    
    twitterCreator = "@avn.systems",
    twitterSite = "@avn.systems",
  }) => (
  <interface>
    <Head>
      <title>{ title }</title>
      <meta charSet='utf-8' />
      <meta name='viewport' content='initial-scale=1.0, width=device-width' />
      <meta name="author" content="Allan Avelar - https://allanavelar.com/" />
      
      <meta property="og:site_name" content={ ogSiteName } />
      <meta property="og:title" content={ ogTitle } />
      <meta property="og:url" content={ ogUrl } />
      <meta property="og:type" content={ ogType } />
      <meta property="og:description" content={ ogDescription } />
      <meta property="og:image" content={ process.env.BACKEND_URL + ogImage } />
    
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:site" content={ twitterSite } />
      <meta name="twitter:creator" content={ twitterCreator } />
    
      <link rel="canonical" href={ canonical } />
      <link rel="alternate" type="application/rss+xml" title={ ogTitle } href={ feedXml } />
      <link rel="alternate" type="application/json" title={ ogTitle } href={ feedJson } />

      <link rel="apple-touch-icon" sizes="114x114" href={ process.env.BACKEND_URL + '/static/images/icon.png'} />
      <link rel="apple-touch-icon" sizes="120x120" href={ process.env.BACKEND_URL + '/static/images/icon.png'} />
      <link rel="apple-touch-icon" sizes="144x144" href={ process.env.BACKEND_URL + '/static/images/icon.png'} />
      <link rel="apple-touch-icon" sizes="152x152" href={ process.env.BACKEND_URL + '/static/images/icon.png'} />
      <link rel="apple-touch-icon" sizes="180x180" href={ process.env.BACKEND_URL + '/static/images/icon.png'} />
      <link rel="icon" type="image/png" href={ process.env.BACKEND_URL + '/static/images/icon.png'} />
      <link rel="shortcut icon" href={ process.env.BACKEND_URL + '/static/images/favicon.ico'} />
      
      <link rel="mask-icon" href="/assets/safari-pinned-tab.svg" color="#5bbad5" />
      <link rel="manifest" href={ process.env.BACKEND_URL + '/static/manifest.json'} />
      
      <meta name="msapplication-TileColor" content="#5700AE" />
      <meta name="msapplication-TileImage" content={ process.env.BACKEND_URL + '/static/images/avn.systems.og-image.jpg'} />
      <meta name="msapplication-config" content={ process.env.BACKEND_URL + '/static/browserconfig.xml'} />
      <meta name="theme-color" content="#5700AE" />
      
      <link defer rel='stylesheet' href='https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0-beta.3/css/bootstrap.min.css' integrity='sha384-Zug+QiDoJOrZ5t4lssLdxGhVrurbmBWopoEl+M6BdEfwnCJZtKxi1KgxUyJq13dy' crossOrigin='anonymous' />
      <link rel='stylesheet' type='text/css' href={ process.env.BACKEND_URL + '/static/css/nprogress.css'} />
      <script defer src="https://code.jquery.com/jquery-3.3.1.min.js"></script>
    </Head>
    <Header />
    <LeftMenu />
    <page>
        { children }
    </page>
    <menuhider></menuhider>
    <Footer />
    <pageloader><bar></bar></pageloader>
    
    <style jsx global>{`
        body {
            min-width: 320px;
            background: url(${process.env.BACKEND_URL}/static/images/body-bg.jpg) #F5F7F6;
        }
        page {
            display: none;
            transition: margin-left 350ms ease 0s;
        }
        .ftl {
            float: left;
        }
        .ftr {
            float: right;
        }
        ::-webkit-scrollbar {
            display: none;
        }
        
        body.left-menu leftmenu {
            left: 0;
        }
        body.left-menu header navtoggle bar:first-child {
            transform: rotate(45deg);
            position: relative;
            top: 15px;
        }
        body.left-menu header navtoggle bar:nth-child(2) {
            transform: rotate(135deg);
            background: #000000;
            position: relative;
            top: 5px;
        }
        body.left-menu header.resize navtoggle bar:nth-child(2) {
            background: #f4f4f4;
        }
        body.left-menu header navtoggle bar:nth-child(3) {
            opacity: 0;
        }
        body.loaded menuhider {
            transition: display 1s ease 1s;
            display: none;
            opacity: 0;
        }
        body.loaded pageloader {
            display: none;
        }
        menuhider {
            background-color: rgba(0,0,0,.5);
            pointer-events: all;
            position: fixed;
            display: block;
            z-index: 1030;
            opacity: 1;
            bottom: 0;
            right: 0;
            left: 0;
            top: 0;
            -webkit-transition: opacity 1s ease 0s;
            -moz-transition: opacity 1s ease 0s;
            -ms-transition: opacity 1s ease 0s;
            -o-transition: opacity 1s ease 0s;
            transition: opacity 1s ease 0s;
        }
        
        .nprogress-busy body.loaded pageloader {
            display: block;
        }
        pageloader {
            margin: -15px 0 0 -100px;
            border-radius: 8px;
            background: #000;
            z-index: 9999999;
            position: fixed;
            width: 200px;
            height: 30px;
            left: 50%;
            top: 50%;
        }
        pageloader bar {
            margin: -2px 0 0 -87px;
            animation: gradtranslate 2s infinite linear;
            background-image: -webkit-gradient(linear,left top,right top,color-stop(0%,white),color-stop(50%,#8500FF),color-stop(100%,white));;
            background-size: 400px 1px;
            background-color: #8500FF;
            border-radius: 5px;
            position: absolute;
            width: 174px;
            opacity: .8;
            height: 4px;
            left: 50%;
            top: 50%;
        }
        
        body.loaded leftmenu,
        body.loaded header,
        body.loaded footer,
        body.loaded page {
            display: block;
        }
        
        @media (min-width: 1250px) {
            body.left-menu header {
                width: calc(100% - 330px);
                margin-left: 330px;
            }
            body.left-menu page {
                margin-left: 330px;
            }
        }
        
        @media (max-width: 1590px) {
            body.left-menu content {
                max-width: calc(100% - 60px);
            }
        }
        @media (max-width: 1250px) {
            body.left-menu header logo {
                left: 400px!important;
            }
            body.left-menu header navtoggle {
                left: 345px!important;
            }
            .nprogress-busy body.loaded menuhider,
            body.loaded.left-menu menuhider {
                display: block;
                opacity: 1;
            }
        }
        @media screen and (max-width: 625px) {
            body.left-menu header navtoggle bar:nth-child(2) {
                background: #f4f4f4;
            }
        }
        @media screen and (max-width: 399px) {
            body.left-menu leftmenu, leftmenu menuscroll {
                width: calc(100% - 69px)!important;
                max-width: 330px;
                min-width: 260px;
            }
        }
    `}</style>
  </interface>
)